import React from 'react'
import Helmet from 'react-helmet'
import 'font-awesome/css/font-awesome.min.css';
import Link from 'gatsby-link'

const Contact = () => (
  <div className="page contact-page">
    <Helmet
      title="Contact to Kacper Cieciński Junior Web Developer"
      meta={[
        { name: 'description', content: 'Contact with me, Kacper Cieciński' },
        { name: 'keywords', content: 'Kacper, Cieciński, Kacper Cieciński, Ciecinski, developer, junior developer,junior react developer, junior front-end developer,front-end,frontend,backend,junior backend developer,junior rails developer,React,Ruby on Rails,Javascript, HTML5, CSS, Contact, Facebook,LinkedIn,Kacper Cieciński LinkedIn, Github,Kacper Cieciński Github,kciecinski,kciecinski Github, email Kacper Cieciński,ciecinski.kacper@gmail.com' },
       ]}
    />

    <h1>Get in touch_</h1>
    <div className="row-center contact-center">
    <div className="col-md-12">
    <a href="mailto:ciecinski.kacper@gmail.com" className="mail"><span>EMAIL:</span> ciecinski.kacper@gmail.com</a>
    <div>
    <a href="https://www.facebook.com" className="social-link">Facebook</a>
    </div>
    <div>
    <a href="https://www.twitter.com" className="social-link">Twitter</a>
    </div>
    <div>
    <a href="https://www.linkedin.com"className="social-link">LinkedIn</a>
    </div>
    <div>
    <a href="https://www.github.com"className="social-link">Github</a>
    </div>
    </div>
    </div>
  </div>
)

export default Contact


